import { newE2EPage } from '@stencil/core/testing';

describe('presto-player-skeleton', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-player-skeleton></presto-player-skeleton>');

    const element = await page.find('presto-player-skeleton');
    expect(element).toHaveClass('hydrated');
  });
});
